@font-face {
  font-family: "Phos";
  src: url("https://files.jcink.net/uploads/noatest001/Phoss___.ttf")
    format("TrueType");
}
@font-face {
  font-family: 'Aqua';
  src: url('https://files.jcink.net/uploads/novacodes/aqua.ttf'); }

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #1b1b1b;
}
::-webkit-scrollbar-thumb {
  background: var(--color);
}

body { 
  background: #1b1b1b;
  background-size: cover;
  background-attachment: fixed;
  font: 11px/100% 'Calibri', sans-serif;
  color: #cdcdcd; 
  margin: 0;
  --color: #4d6d9a;
  --color3: rgba(77,109,154,0.3); 
  --color5: rgba(77,109,154,0.5); 
  --color7: rgba(77,109,154,0.7);
  --black3: rgba(0,0,0,0.3);
  --black4: rgba(0,0,0,0.4);
  --black5: rgba(0,0,0,0.5);
  --black7: rgba(0,0,0,0.7);
  --light3: rgba(239,239,239,0.3);
  --light5: rgba(239,239,239,0.5);
  --light7: rgba(239,239,239,0.7); 
  --dark: #1b1b1b; 
  --dark0: rgba(27,27,27,0);
  --dark3: rgba(27,27,27,0.3);
  --dark5: rgba(27,27,27,0.5);
  --dark7: rgba(27,27,27,0.7); }

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: var(--color);
}

.wrapper {
  width: 100%;
  padding: 100px 0;
  position: relative;
  top: 100vh;
  left: 0;
  z-index: 98; 
  box-sizing: border-box;
  background: #1b1b1b url(/static/media/bg.adf1284d.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: 0 0;
  background-position: initial; }

.innerwrapper {
  width: calc(100% - 200px);
  margin: 0 auto;
  padding: 100px 0;
  background: var(--black4);
  outline: 1px solid var(--light3); }

h1 {
  text-align: center;
  padding: 40px 0 0 0; 
  font: 40px/100% 'Aqua', serif;
  color: var(--color);
  text-transform: uppercase; }
  
h2 {
  text-align: center;
  font: 40px/100% 'Martel', serif;
  color: var(--light7);
  text-transform: uppercase; }
  
h3 {
  text-align: center;
  font: 11px/100% 'Livvic', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--color7); }
html.sr .load-hidden {
    visibility: hidden;
}

header {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center; 
    z-index: 9;
    background: linear-gradient(to top, var(--black7), var(--color3)), 
                url(/static/media/stars.e17a41b7.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed; }

header > big {
    margin: 0;
    position: absolute;
    font: 80px/200% 'Aqua', serif;
    color: var(--light7); 
    letter-spacing: 50px;
    text-transform: uppercase;
    text-shadow: 2px 2px 20px rgba(63,63,63,0.7);
    mix-blend-mode: screen; }
    
    header > big span {
    color: var(--color7);
    animation: flicker 4s infinite; }
    
    header > big span:nth-of-type(1) {
    animation-delay: 1s; }
    
    header > big span:nth-of-type(3) {
    animation-delay: 1.7s; }
            
    header > big:before {
    content: '';
    border-left: 1px solid var(--light5);
    border-top: 1px solid var(--light5); 
    background: transparent;
    display: flex;
    width: 50px;
    height: 50px;
    position: absolute;
    left: -40px;
    top: -5px;
    -webkit-transform: skew(0deg); }
    
    header > big:after {
    content: '';
    border-right: 1px solid var(--light5);
    border-bottom: 1px solid var(--light5); 
    background: transparent;
    display: flex;
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: -10px;
    right: -10px;
    -webkit-transform: skew(0deg); }

    @keyframes flicker {
        0% {
          opacity: 1; }
        31.8% {
          opacity: 0.9; }
        32% {
          opacity: 0; }
        32.2% {
          opacity: 0.9; }
        94.6% {
          opacity: 1; }
        95% {
          opacity: 0; }
        95.4% {
          opacity: 0.9; }
        100% {
          opacity: 1; } }

.catwrap {
    width: calc(100% - 220px);
    margin: 0 auto 100px;
    position: relative;
    outline: 1px solid var(--black7);
    background: var(--black3);
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: space-between; }
    
.catwrap:before {
    content: '';
    border-left: 1px solid var(--light3);
    border-top: 1px solid var(--light3);
    display: flex;
    padding: 5px;
    position: absolute;
    top: -10px;
    left: -10px;
    width: 50px;
    height: 50px; }
    
.catwrap:after {
    content: '';
    border-right: 1px solid var(--light3);
    border-bottom: 1px solid var(--light3);
    display: flex;
    padding: 5px;
    position: absolute;
    bottom: -10px;
    right: -10px;
    width: 50px;
    height: 50px; }

.maintitle {
    align-self: stretch;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    padding: 30px 5px;
    text-align: right;
    letter-spacing: 3px;
    word-spacing: 6px; 
    position: relative;
    background: linear-gradient(to top, var(--dark3), var(--color7));
    border-right: 1px solid var(--black7); }
        
.maintitle a:link,
.maintitle a:active,
.maintitle a:visited {
    font: 20px/130% 'Martel', serif; 
    text-transform: uppercase;
    color: var(--light7);
    text-shadow: -2px -2px 10px var(--light5); 
    transform: rotate(180deg);
    position: -webkit-sticky;
    position: sticky;
    top: 130px;
    margin-left: 30px;
    padding-left: 30px; }
        
.maintitle a:hover {
    color: var(--light); }

.fwrap-flex {
    width: calc(100% - 60px);
    display: flex;
    flex-wrap: wrap;
    align-items: center; 
    justify-content: space-evenly; }

.fwrap-flex img {
    object-fit: cover;
    min-width: 15%; 
    max-width: 50%;
}

.fwrap {
    width: 100%;
    background: linear-gradient(to top right, var(--black3), var(--dark7)); 
    min-height: 270px;
    border-bottom: 1px solid var(--black5); }
    
.fwrap:last-child { border: 0; }

.forumname {
    width: 90%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px auto 0px; }
  
  .forumname a:link,
  .forumname a:active,
  .forumname a:visited {
    font: 30px/100% 'Aqua', serif;
    color: var(--light7);
    text-transform: uppercase;
    padding-bottom: 10px;
    letter-spacing: 3px;
    text-shadow: 2px 2px 20px var(--color5);
    mix-blend-mode: screen; 
    transition: color .5s ease-in-out; }
    
  .forumname a:hover { color: var(--color7); }
    
  .forumname a > span {
    color: var(--light5);
    font-size: 25px; }
    
  .forumname.forum-new-post a > span {
    color: var(--color7);
    animation: flicker 4s infinite; }
  
  .forumdesc {
    width: calc(100% - 150px);
    max-height: 35px;
    overflow: auto;
    margin: 0 auto;
    border: 1px solid var(--color5);
    padding: 15px;
    text-align: justify;
    font: 11px/140% 'Livvic', sans-serif;
    letter-spacing: 0.5px;
    word-spacing: 2px; }
  
  .subforums {
    width: calc(100% - 120px);
    min-height: 30px;
    background: var(--color5);
    margin: 5px auto; 
    display: flex;
    flex-wrap: wrap; 
    align-items: center;
    justify-content: space-evenly;
    align-content: space-evenly;
    outline: 1px solid var(--color7); }
    
  .subforums a {
    padding: 9px;
    font: 11px/100% 'Martel', serif;
    text-transform: uppercase; 
    color: var(--light7);
    text-shadow: 1px 1px 5px var(--light5); }
  
  .lastpost {
    width: calc(100% - 120px);
    height: 45px;
    margin: 5px auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-flow: column nowrap; }
    
  .lastpost span {
    font: 12px/100% 'Aqua', serif;
    letter-spacing: 1px;
    text-transform: uppercase;
    padding: 3px; }
    
  .lastpost span a {
    color: var(--color); }
        
  .lastpost span:nth-of-type(2) {
    font: 10px/100% 'Livvic', sans-serif;
    color: #cdcdcd; }
    
